.App {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.todoList-newTaskForm {
  display: flex;
  justify-content: center;
  margin-top: 10px;
}

.todoList {
  width: 300px;
  border: 2px ridge grey;
  margin: 10px;
}

.todoList-header__title {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.todoListFooterButton {
  display: flex;
  justify-content: space-around;
  padding: 10px;
  width: 90%;
}

.todoListIsHidden {
  width: 90%;
  display: flex;
  justify-content: center;

}

.todoListIsShow {
  width: 90%;
  display: flex;
  justify-content: center;
}

.todoListFooterSpan {
  width: 30%;
  border: 1px ridge grey;
  text-align: center;
}

.buttonAddTodoLists {
  margin-left: 10px;
}

.buttonHeaderTitle {
  margin-left: 10px;
  height: 25px;
}

.todoList > div {
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.loginWrapper {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.inputEmail {
  margin-bottom: 10px;
}

.inputPassword {
  margin-bottom: 10px;
}

.buttonLogOut {
  position: absolute;
  top: 10px;
}

.todoList-task.done {
  opacity: 0.5;
}

.filter-active {
  background-color: blue;
}

.error {
  border-color: red;
}